import React from "react";
// react component for creating beautiful carousel
import Carousel from "react-slick";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import LocationOn from "@material-ui/icons/LocationOn";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import SlideImages from "views/Components/Custom/SlideImages";

import image1 from "assets/img/bg.jpg";
import image2 from "assets/img/bg2.jpg";
import image3 from "assets/img/bg3.jpg";

import styles from "assets/jss/material-kit-react/views/componentsSections/carouselStyle.js";

const useStyles = makeStyles(styles);

export default function SectionCarousel(props) {
  const classes = useStyles();
//   console.log(props);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: false,  
    afterChange: function(index) {
        var img=index==0?image1:index==1?image2:index==2?image3:""
        props.bg_image(`${img}`);
        // console.log(img
        // //   `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
        // );
      }
  };
  return (
    <div className={classes.section} >
      <div className={classes.home_container}>
        <GridContainer>
          <GridItem xs={12} sm={12} md={11} className={classes.marginAuto}>
            <Card carousel>
              <Carousel {...settings} >
                <div>
                    <SlideImages image={image1} alt="First slide"/> 
                  {/* <img src={image1} alt="First slide" className="slick-image" /> */}
                  <div className="slick-caption">
                    <h4>
                      <LocationOn className="slick-icons" />
                      Yellowstone National Park, United States
                    </h4>
                  </div>
                </div>
                <div>
                <SlideImages image={image2} alt="Second slide"/> 
                  {/* <img
                    src={image2}
                    alt="Second slide"
                    className="slick-image"
                  /> */}
                  <div className="slick-caption">
                    <h4>
                      <LocationOn className="slick-icons" />
                      Somewhere Beyond, United States
                    </h4>
                  </div>
                </div>
                <div>
                  {/* <img src={image3} alt="Third slide" className="slick-image" /> */}
                  <SlideImages image={image3} alt="Third slide"/> 
                  <div className="slick-caption">
                    <h4>
                      <LocationOn className="slick-icons" />
                      Yellowstone National Park, United States
                    </h4>
                  </div>
                </div>
              </Carousel>
            </Card>
          </GridItem>
        </GridContainer>
      </div>
    </div>
  );
}
